html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --primary-color: #ffcb10;
  --secondary-color: #00d8b4;
  --background: #181818;
  --third-color: #a3a3a3;
}

::selection {
  background-color: var(--background);
  color: #fff;
  text-shadow: none;
}

html {
  font-size: 1vw;
}

body {
  background-color: var(--background);
  color: #fff;
  height: 100vh;
}

.o-page {
  width: 80%;
  margin: 0 auto 40px;
  padding-top: 123px;
  font-family: Silkscreen, Arial, Helvetica, sans-serif;
}

o-layout {
  margin: 0 auto;
}

.o-layout_item {
  width: 49%;
}

.o-layout_item img {
  width: 100%;
}

.o-layout_item p {
  text-align: right;
  font-size: 2.5rem;
  line-height: 1.34;
}

p.o-text {
  text-align: left;
}

.o-background {
  height: 129px;
}

.o-header {
  display: inline-block;
}

.o-title {
  text-align: center;
  color: var(--secondary-color);
  padding: 25px 0;
  font-size: 5rem;
}

.o-page .o-figure {
  width: 540px;
  margin: 0 auto;
}

.o-page .o-figure figcaption {
  text-align: center;
  padding: 15px 0;
}

.video-container {
  position: relative;
}

.video-container video {
  width: 100%;
}

.custom-controls {
  background-color: #000000b3;
  justify-content: center;
  align-items: center;
  padding: 8px;
  display: flex;
  bottom: 10px;
  left: 0;
  right: 0;
}

.custom-controls button, .custom-controls input {
  color: #fff;
  cursor: pointer;
  background: none;
  border: none;
  margin: 0 5px;
  padding: 5px;
  font-size: 16px;
}

.custom-controls input[type="range"] {
  width: 100px;
}

.custom-controls span {
  color: #fff;
  margin: 0 5px;
  font-size: 14px;
}

@media (max-width: 1023px) {
  .o-page {
    width: 90%;
  }

  .o-layout_item p {
    line-height: 1.2;
  }

  html {
    font-size: 1.2vw;
  }

  .o-page .o-figure figcaption {
    font-size: 15px;
  }
}

@media (max-width: 699px) {
  .o-page .o-figure {
    width: 100%;
    margin: 0 auto;
  }
}

.l-header {
  padding: 40px 0 20px;
  font-size: 3rem;
}

.l-link-item {
  padding: 20px 0;
}

.l-link-item h2 {
  padding: 0 0 10px;
  font-size: 1.5em;
}

.l-link-item a {
  color: var(--secondary-color);
  padding: 0 0 10px;
  text-decoration: none;
  display: block;
}

.l-link-item p {
  line-height: 1.2;
}

.l-link-item:nth-child(odd) {
  text-align: right;
}

.l-link-item:nth-child(odd) a {
  color: var(--primary-color);
}

.l-link-item a:hover {
  color: #fff;
}

.l-text {
  width: 75%;
  display: inline-block;
}

#categories, #links, #detail {
  width: 90%;
}

#categories section {
  padding: 0 0 30px;
}

.e-container {
  grid-template-columns: 25% 25% 25% 25%;
  display: grid;
}

.episode {
  max-width: 100%;
  margin: 6px;
}

.e-thumbnail {
  margin: 0;
  position: relative;
}

.e-thumbnail img {
  width: 100%;
}

.e-thumbnail-gradient {
  background-image: linear-gradient(0deg, #181818cc 5%, #0000 95%);
  height: 65px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: -.5px;
}

.e-data .e-title {
  font-size: 16px;
  font-weight: 500;
}

.e-runtime {
  color: #a3a3a3;
  font-size: 16px;
  line-height: 27px;
}

.e-synopsis {
  color: #a3a3a3;
  font-family: Silkscreen, Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 15px;
}

#categories h2 {
  font-size: 30px;
  line-height: 65px;
}

section#voices .v-container {
  grid-template-columns: 25% 75%;
  display: grid;
}

@media (max-width: 1439px) {
  .e-container {
    grid-template-columns: 33.333% 33.333% 33.333%;
  }
}

@media (max-width: 1023px) {
  .e-container {
    grid-template-columns: 50% 50%;
  }
}

@media (max-width: 599px) {
  .e-container {
    grid-template-columns: 100%;
  }
}

.f-container, .f-item {
  grid-template-columns: 50% 50%;
  display: grid;
}

.f-thumbnail {
  width: 250px;
}

.f-thumbnail img {
  width: 100%;
}

.episode a {
  color: #fff;
  text-decoration: none;
}

.episode .e-runtime a, .episode .e-synopsis a {
  color: var(--third-color);
  text-decoration: none;
}

.episode a:hover {
  color: var(--secondary-color);
  text-decoration: none;
}

.episode .e-synopsis a:hover {
  color: var(--primary-color);
  text-decoration: none;
}

#detail .details-info {
  grid-template-columns: 50% 50%;
  place-items: center;
  display: grid;
}

#detail h2 {
  font-size: 40px;
  line-height: 1;
}

.details-image-top img {
  width: 100%;
}

.details-image-top figcaption {
  text-align: center;
  padding: 5px 0 20px;
  font-size: 1.25em;
}

.details-container {
  margin-left: 20px;
}

.title-year {
  color: var(--third-color);
  margin: 10px 0;
  font-size: 30px;
  display: block;
}

.title-info {
  color: var(--primary-color);
  margin: 10px 0;
  font-size: 20px;
}

.title-data-info {
  padding: 25px 0;
  font-size: 16px;
}

.title-data {
  color: var(--primary-color);
}

#detail-text .detail-data {
  padding: 10px 0;
  font-size: 1.15em;
  line-height: 1.1;
}

@media (max-width: 999px) {
  #detail .details-info {
    grid-template-columns: 100%;
    display: grid;
  }
}

i.fa-play {
  margin-left: 3px;
  font-size: .75em;
}

.actor-list dd {
  opacity: 0;
  max-height: 0;
  margin-left: 0;
  transition: opacity .3s, max-height .3s;
  display: none;
  overflow: hidden;
}

.actor-list dt {
  cursor: pointer;
  margin: 10px 0 0;
}

.actor-list dt:hover {
  color: var(--secondary-color);
}

.actor-list dt.active + dd {
  opacity: 1;
  max-height: 100px;
  display: block;
}

.marked-actor {
  color: var(--secondary-color);
}

.character-container {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.character-card {
  text-align: center;
  width: 150px;
  padding: 25px;
}

.character-card img {
  width: 100%;
  margin-bottom: 10px;
}

.character-card h3 {
  color: var(--secondary-color);
  margin-bottom: 10px;
  font-size: 18px;
}

hgroup h2 {
  color: var(--primary-color);
  margin: 0 0 5px;
  line-height: 1 !important;
}

hgroup p {
  color: var(--third-color);
  font-size: 16px;
}

#info-media-plot > p {
  margin: 15px 0 0;
}

.container {
  align-items: center;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
}

.c-header_list_item {
  vertical-align: top;
  font-family: Silkscreen, Arial, Helvetica, sans-serif;
  font-size: 16px;
  display: inline-block;
}

.c-header_list_item:nth-child(2) {
  padding: 0 18vw;
}

.c-header {
  z-index: 50;
  background-color: var(--background);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.c-header_nav {
  margin: 0 auto;
}

.c-header_nav a {
  padding: 0 0 15px;
}

.c-header_container {
  justify-content: space-between;
  align-items: center;
  height: 100%;
  display: flex;
}

.c-header_list_item a {
  color: var(--secondary-color);
  text-transform: uppercase;
  text-decoration: none;
}

@media (max-width: 1023px) {
  .c-header_list_item:nth-child(2) {
    padding: 0 10vw;
  }
}

@media (max-width: 999px) {
  .c-header_list_item:nth-child(2) {
    padding: 0;
  }

  .c-header_list_item {
    text-align: right;
    display: block;
  }

  .c-header_nav {
    margin: 0 25px 0 0;
  }

  .c-header_nav a {
    margin: 0 0 10px;
    padding: 0 0 5px;
  }
}

.hvr-underline-from-left:before {
  background: var(--secondary-color) !important;
  height: 2px !important;
}

.footer {
  color: #fff;
  text-align: center;
  background-color: #181818;
  padding: 20px 0;
  font-family: Silkscreen, Arial, Helvetica, sans-serif;
  font-size: 16px;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer p {
  margin: 5px 0;
}

.heart {
  color: red;
}

time {
  color: var(--primary-color);
}

@media (max-width: 1023px) {
  .footer {
    font-size: 13px;
  }
}

@media (max-width: 999px) {
  .footer {
    font-size: 11px;
  }
}



